
import React, {Component} from 'react';
import "./style.css"

class Form extends React.Component {
	
	constructor(props){
		super(props);
		
		this.state = { login :'',
					  password: '', 
					  dateOfArrival : '',
					  dateOfDeparture : '',
					  telNumber : ' '	
					 };
		
		this.onLoginChange = this.onLoginChange.bind(this);
		
		this.onPasswordChange = this.onPasswordChange.bind(this);
		
		this.ondateOfArrivalChange = this.ondateOfArrivalChange.bind(this);
		
		this.ondateOfDepartureChange = this.ondateOfDepartureChange.bind(this);
		
		this.ontelNumberChange = this.ontelNumberChange.bind(this);
		
		this.onSubmit = this.onSubmit.bind(this);
						
	}

	onSubmit(e){ //обработчик события сабмит
		
			
	//let obj = JSON.stringify ({ login : this.state.login , password : this.state.password });
		
		let date = { name : this.state.login,
					password : this.state.password,
					dateOfArrival : this.state.dateOfArrival,
					dateOfDeparture: this.state.dateOfDeparture,
					telNumber : this.state.telNumber
				   		
				   };
		
		let myHeaders = new Headers ({ "KoSiteKey" : "test198" })
		
		let myInit = {
			method : 'POST',
			headers : myHeaders,
			mode : 'cors',
			cache : 'default',
			data : JSON.stringify(date)
		};
		
		const url = 'https://ko.tour-shop/siteLead';
		
		fetch('url', myInit)
		.then ((res)=>{
	
			if (res.status == 403 ) console.log ('неверный доступ');
			
			if (res.status == 404 ) console.log ('неверный доступ');
			
			if (res.status == 200){
				
					
				
			}
			console.log(res.type)
			console.log(res ,'1')
			return res.data
		})
		.then((data)=>{ console.log(data) })
		.catch ((err)=>{
			console.log(err)
		})
		
		
		console.log(date , 'сабмит формы')
		
		e.preventDefault();	 
	}
	
	
	
	onPasswordChange(e){
		this.setState({password: e.target.value});
	}
	
	onLoginChange(e) {
      	this.setState({login: e.target.value});
    }
	
	ondateOfArrivalChange(e){
		this.setState({dateOfArrival: e.target.value})
	}
	
	ondateOfDepartureChange(e){
		this.setState({dateOfDeparture: e.target.value})
	}
	
	ontelNumberChange(e){
		this.setState({telNumber: e.target.value})
	}

	
	
	
    render() {
      return (
 
        <form className = 'f1' onSubmit={this.onSubmit}>
	      
		  <p><label> Логин: <input type="text" name="login" value={this.state.login}
                           onChange={this.onLoginChange}/></label></p>
		  
		  <p><label> Пароль: <input type="text" name="password" value={this.state.password}
                            onChange={this.onPasswordChange}/></label></p>
		  
		  
		  
		  <p><label> Дата прибытия : <input type="text" name="login" value={this.state.dateOfArrival}
                           onChange={this.ondateOfArrivalChange}/></label></p>
		  
		  
		  
		  
          <p><label> Дата отьезда: <input type="text" name="password" value={this.state.dateOfDeparture}
                            onChange={this.ondateOfDepartureChange}/></label></p>
		  
		   <p><label> Номер телефона: <input type="text" name="password" value={this.state.telNumber}
                            onChange={this.ontelNumberChange}/></label></p>
          
		  <p><input type="submit" value="Submit" /></p>
        </form>
      )
    }
	
	
	
}



export default Form




